/* ==========================================================================
   Portfolio Single - Small Slider layout style - begin
   ========================================================================== */

.eltdf-portfolio-single-holder {
	
	&.eltdf-ps-small-slider-layout {
		
		.eltdf-ps-image-holder {
			
			.eltdf-ps-image-inner {
				visibility: hidden;
			}
			
			.eltdf-ps-image {
				
				img {
					width: 100%;
				}
			}
		}
		
		.eltdf-ps-content-item {
			margin: 0 0 35px;
		}

		.eltdf-ps-navigation {
            margin: 73px 0 22px;
        }
	}
}
/* ==========================================================================
   Portfolio Single - Small Slider layout style - end
   ========================================================================== */